
import { defineComponent, ref, getCurrentInstance } from "vue";
import jsonToObject from "./mixins/forLoopMixin";
import {
  System,
  Banner,
  Category,
  Item,
  LSCustomerInfo,
  CRUD,
  OrderStatus,
  Order,
  PostOrUpdate,
} from "@/interface";
import ItemDetailComponent from "./components/ItemDetailComponent.vue";
import FooterComponent from "./components/FooterComponent.vue";
import OrderListModalComponent from "./components/OrderListModalComponent.vue";
import AlertModalComponent from "./components/AlertModalComponent.vue";
import OrderHistoryModalComponent from "./components/OrderHistoryModalComponent.vue";
import {
  getCustomerInfoFromLS,
  getOrderListFromLS,
  removeOrderListFromLS,
  setCustomerInfoToLS,
  setOrderListToLS,
} from "./mixins/localStorageMixin";
import { fetchPost, fetchPostByType } from "./mixins/fetchMixin";
import {
  formatToGoogleSheetDate,
  formatToJSDate,
} from "./mixins/formatDateMixin";
import { convertGoogleDriveImage } from "./mixins/generalMixin";

export default defineComponent({
  components: {
    ItemDetailComponent,
    FooterComponent,
    OrderListModalComponent,
    AlertModalComponent,
    OrderHistoryModalComponent,
  },
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref({
      url: proxy.$getUrl,
      fetchCode: proxy.$fetchCode,
    });

    let systemData = ref<System>({
      id: 1,
      companyName: "...",
      companyAddress: "",
      companyContact: "",
      companyDescription: "",
      serviceCharge10Percent: false,
      tax6Percent: false,
    });
    let banners = ref<Banner[]>([
      { id: 1, image: "", description: "", itemId: 0 },
    ]);
    let categories = ref<Category[]>([]);
    let items = ref<Item[]>([]);
    let orderHistory = ref<Order | null>();
    let orderList = ref<Item[]>([]);
    let customerInfo = ref<LSCustomerInfo | null>();
    let isLoading = ref<boolean>(true);

    let fetchCategories = fetch(
      `${proxyData.value.url}tqx=out:json&sheet=Categories`
    );
    let fetchBanners = fetch(
      `${proxyData.value.url}tqx=out:json&sheet=Banners`
    );
    let fetchItems = fetch(`${proxyData.value.url}tqx=out:json&sheet=Items`);
    let fetchSystem = fetch(`${proxyData.value.url}tqx=out:json&sheet=System`);
    let fetchOrderHistories;

    const dataCustomerInfo = getCustomerInfoFromLS();
    if (dataCustomerInfo) {
      fetchOrderHistories = fetch(
        `${proxyData.value.url}sheet=Orders&tq=${encodeURIComponent(
          "select * where K = "
        )}${dataCustomerInfo.deviceId} and C != ${encodeURIComponent(
          `"${OrderStatus.completed}"`
        )}`
      );
      customerInfo.value = dataCustomerInfo;
    }

    // removeOrderListFromLS(); // TODO

    console.log(5);

    Promise.all([
      fetchCategories,
      fetchBanners,
      fetchOrderHistories,
      fetchItems,
      fetchSystem,
    ]).then((res) => {
      // categories => banners => orderHistory => items
      // res[0] he res[1] 不知道为什么不能一起 run， 所以只好层中层
      res[0].text().then((rep) => {
        const dataCategories = jsonToObject(rep);
        dataCategories.forEach((c: Category) => (c.items = []));

        console.log(4);

        // banners
        res[1].text().then((rep) => {
          const dataBanners = jsonToObject(rep);

          console.log(3);

          // orderHistory
          if (res[2]) {
            res[2].text().then((rep) => {
              let dataOrderHistory = jsonToObject(rep)[0];

              console.log(2);

              // items
              res[3].text().then((rep) => {
                const dataItems = jsonToObject(rep);
                const dataOrderList = getOrderListFromLS();
                const updatedOrderList: Item[] = [];

                // 先处理 orderHistory
                if (dataOrderHistory) {
                  const orderHistoryCreatedDate = formatToJSDate(
                    dataOrderHistory.createdDate
                  );
                  const betweenMinute =
                    (new Date().getTime() - orderHistoryCreatedDate.getTime()) /
                    1000 /
                    60;

                  // auto update status if more than 2 hour
                  if (betweenMinute < 120) {
                    dataOrderHistory.items = JSON.parse(
                      dataOrderHistory.items as string
                    ) as Item[];
                  } else {
                    dataOrderHistory.status = OrderStatus.completed;
                    dataOrderHistory.note = `Error! Status updated to "Completed" due to order over 2 hours!`;
                    dataOrderHistory.createdDate = formatToGoogleSheetDate(
                      new Date(formatToJSDate(dataOrderHistory.createdDate))
                    );

                    console.log("auto update item status");
                    fetchPost<Order>(
                      CRUD.update,
                      "Orders",
                      proxyData.value.fetchCode,
                      dataOrderHistory
                    );

                    dataOrderHistory = null;
                  }
                }

                // 再处理 items's banner & orderHistory & orderList
                dataItems.forEach((item: Item) => {
                  const foundCategory = dataCategories.find(
                    (c: Category) => c.name === item.category
                  );

                  item.added = 0;
                  item.image = convertGoogleDriveImage(item.image);
                  foundCategory.items.push(item);

                  dataBanners.forEach((banner: Banner) => {
                    banner.image = convertGoogleDriveImage(banner.image);
                    if (banner.itemId === item.id) {
                      banner.item = item;
                    }
                  });

                  if (dataOrderHistory) {
                    dataOrderHistory.items.forEach((orderHistoryItem: Item) => {
                      if (orderHistoryItem.id === item.id) {
                        item.isPrevOrder = true;
                      }
                    });
                  }

                  if (dataOrderList) {
                    dataOrderList.forEach((orderListItem: Item) => {
                      if (orderListItem.id === item.id) {
                        item.added = orderListItem.added;
                        updatedOrderList.push(item);
                      }
                    });
                  }
                });

                console.log(1);

                categories.value = dataCategories;
                banners.value = dataBanners;
                orderHistory.value = dataOrderHistory;
                orderList.value = updatedOrderList;
                items.value = dataItems;
                isLoading.value = false;
              });
            });
          }
        });
      });

      // system
      res[4].text().then((rep) => {
        systemData.value = jsonToObject(rep)[0];
        window.document.title = `${systemData.value.companyName} - Menu Panda`;
      });
    });

    return {
      proxyData,
      systemData,
      banners,
      categories,
      orderHistory,
      items,
      orderList,
      customerInfo,
      isLoading,
    };
  },
  data() {
    return {
      diplayItemId: null as null | number,
      isShowOrderListModal: false,
      isShowOrderListModalLoading: false,
      isShowAlertModal: false,
      isShowOrderHistoryModal: false,
      orderId: 0,
    };
  },
  computed: {
    totalAmount() {
      let result = 0;
      result = this.orderList.reduce((total: number, item: Item) => {
        return (total += item.price * item.added);
      }, 0);
      if (this.orderHistory) {
        result += (this.orderHistory.items as Item[]).reduce(
          (total: number, item: Item) => {
            return (total += item.price * item.added);
          },
          0
        );
      }
      return result;
    },
  },
  methods: {
    showItemDetail(id: number): void {
      this.diplayItemId = id;
    },
    closeItemDetail() {
      this.diplayItemId = null;
    },
    addToOrder(value: { id: number; qty: number }) {
      const item = this.items.find((item: Item) => item.id === value.id)!;
      item.added += value.qty;

      const foundInOrderList = this.orderList.find(
        (item: Item) => item.id === value.id
      );
      if (!foundInOrderList) {
        this.orderList.unshift(item);
      }

      this.orderList.forEach((item: Item, index) => {
        if (item.id === value.id && item.added === 0) {
          this.orderList.splice(index, 1);
        }
      });

      setOrderListToLS(this.orderList);
    },
    showOrderListModal(value: boolean) {
      this.isShowOrderListModal = value;
    },
    removeOrderItem(id: number) {
      this.items.find((item: Item) => item.id === id)!.added = 0;

      let indexOf = this.orderList.findIndex((item: Item) => item.id === id);
      this.orderList.splice(indexOf, 1);

      if (this.orderList.length > 0) {
        setOrderListToLS(this.orderList);
      } else {
        removeOrderListFromLS();
      }
    },
    updateCustomerInfo() {
      let result = getCustomerInfoFromLS();
      if (!result) {
        result = { deviceId: Date.now() } as LSCustomerInfo;
        setCustomerInfoToLS(result);
      }
      this.customerInfo = result;
    },
    orderSubmitted(value: { order: Order; postOrUpdate: PostOrUpdate }) {
      this.isShowOrderListModalLoading = true;
      value.order.deviceId = this.customerInfo!.deviceId;

      if (value.postOrUpdate === PostOrUpdate.post) {
        // post order
        fetchPostByType("createOrder", this.proxyData.fetchCode, value.order)
          .then(() => {
            removeOrderListFromLS();

            // get new order Id
            fetch(
              `${this.proxyData.url}sheet=Orders&tq=${encodeURIComponent(
                "select * where K = "
              )}${this.customerInfo!.deviceId} and C = ${encodeURIComponent(
                `"${OrderStatus.waiting}"`
              )}`
            )
              .then((res) => res.text())
              .then((rep) => {
                this.isShowOrderListModalLoading = false;
                this.orderId = jsonToObject(rep)[0].orderId!;

                setTimeout(() => {
                  this.isShowOrderListModal = false;
                  this.orderList = [];
                  this.items.forEach((item: Item) => (item.added = 0));
                }, 1000);
                setTimeout(() => {
                  this.isShowAlertModal = true;
                }, 2000);
              });
          })
          .catch(() => {
            alert(
              "Error! Submit fail, please inform waiters and order manually!"
            );
          });
      } else {
        // update order
        fetchPost<Order>(
          CRUD.update,
          "Orders",
          this.proxyData.fetchCode,
          value.order
        )
          .then(() => {
            removeOrderListFromLS();
            this.orderId = this.orderHistory!.orderId!;
            setTimeout(() => {
              this.isShowOrderListModal = false;
              this.orderList = [];
              this.items.forEach((item: Item) => (item.added = 0));
            }, 1000);
            setTimeout(() => {
              this.isShowAlertModal = true;
            }, 2000);
          })
          .catch(() => {
            alert(
              "Error! Submit fail, please inform waiters and order manually!"
            );
          });
      }
    },
    showOrderListModalLoading(value: boolean) {
      this.isShowOrderListModalLoading = value;
    },
    showAlertModal(value: boolean) {
      this.isShowAlertModal = value;
    },
    showOrderHistoryModal(value: boolean) {
      this.isShowOrderHistoryModal = value;
    },
  },
  watch: {
    orderHistory(newValue) {
      if (newValue !== null) {
        setTimeout(() => {
          this.isShowOrderHistoryModal = true;
        }, 1000);
      }
    },
  },
});
