
import { Item, Order, OrderStatus, PostOrUpdate } from "@/interface";
import {
  formatToGoogleSheetDate,
  formatToJSDate,
} from "@/mixins/formatDateMixin";
import { padStart3, rounding } from "@/mixins/generalMixin";
import { defineComponent } from "vue";

export default defineComponent({
  props: [
    "isShowOrderListModal",
    "orderList",
    "totalAmount",
    "systemData",
    "isShowOrderListModalLoading",
    "orderHistory",
  ],
  emits: [
    "showOrderListModal",
    "removeOrderItem",
    "updateCustomerInfo",
    "orderSubmitted",
    "showOrderListModalLoading",
  ],
  computed: {
    serviceCharge() {
      return this.systemData.serviceCharge10Percent
        ? this.totalAmount * 0.1
        : 0;
    },
    tax() {
      return this.systemData.tax6Percent ? this.totalAmount * 0.06 : 0;
    },
    roundingAdj() {
      const serviceCharge = parseFloat(this.serviceCharge.toFixed(2));
      const tax = parseFloat(this.tax.toFixed(2));
      return rounding(this.totalAmount + serviceCharge + tax);
    },
    nettTotal() {
      return (
        this.totalAmount + this.serviceCharge + this.tax + this.roundingAdj
      );
    },
  },
  methods: {
    showOrderListModal(value: boolean) {
      this.$emit("showOrderListModal", value);
    },
    removeOrderItem(id: number) {
      this.$emit("removeOrderItem", id);
      if (this.orderList.length === 0) {
        this.showOrderListModal(false);
      }
    },
    confirmOrder() {
      this.$emit("showOrderListModalLoading", true);
      this.$emit("updateCustomerInfo");
      let postOrUpdate = PostOrUpdate.post;
      let postData = {} as Order;

      if (this.orderHistory) {
        const formatZeroes = { ...this.orderHistory.items[0] }.addedList.map(
          () => 0
        );
        this.orderHistory.items.forEach(
          (orderHistoryItem: Item) => orderHistoryItem.addedList!.push(0) // set zero to last position e.g. [1,0]
        );
        console.log(formatZeroes);

        let combineOrderItems: Item[] = [...this.orderHistory.items];

        [...this.orderList].reverse().forEach((orderListItem: Item) => {
          const found = combineOrderItems.filter(
            (combineItem: Item) => combineItem.id === orderListItem.id
          )[0];
          if (found) {
            found.addedList!.pop();
            found.addedList!.push(orderListItem.added);
          } else {
            orderListItem.addedList = [...formatZeroes];
            orderListItem.addedList.push(orderListItem.added);
            combineOrderItems.unshift(orderListItem);
          }
        });

        combineOrderItems.map((item: Item) => {
          const data = { ...item };
          data.added = item.addedList!.reduce(
            (total: number, qty: number) => (total += qty),
            0
          );
          return data;
        });


        postOrUpdate = PostOrUpdate.update;
        postData = {
          id: this.orderHistory.id,
          orderId: this.orderHistory.orderId,
          status: this.orderHistory.status,
          tableNo: 0,
          items: JSON.stringify(combineOrderItems),
          amount: parseFloat(this.nettTotal.toFixed(2)),
          createdDate: formatToGoogleSheetDate(
            new Date(formatToJSDate(this.orderHistory.createdDate))
          ),
          deviceId: this.orderHistory.deviceId,
          note: this.orderHistory.note,
        } as Order;
      } else {
        this.orderList.forEach((item: Item) => {
          item.addedList = [item.added];
        });
        postData = {
          id: 0, // 0 = get max id from GS
          status: OrderStatus.waiting,
          tableNo: 0,
          items: JSON.stringify(this.orderList),
          amount: parseFloat(this.nettTotal.toFixed(2)),
          createdDate: formatToGoogleSheetDate(new Date()),
          deviceId: 0, // let outside update
          note: "",
        } as Order;
      }

      this.$emit("orderSubmitted", { order: postData, postOrUpdate });
    },
    padStart(val: number) {
      return padStart3(val);
    },
  },
});
