
import { padStart3 } from "@/mixins/generalMixin";
import { defineComponent } from "vue";

export default defineComponent({
  props: ["isShowAlertModal", "orderId"],
  emits: ["showAlertModal"],
  methods: {
    padStart(val: number) {
      return padStart3(val);
    },
    showAlertModal(value: boolean) {
      this.$emit("showAlertModal", value);
      setTimeout(() => {
        window.location.reload();
      }, 1000);
    },
  },
});
